.portfolios-data{
    margin-top: 2rem;
}

.portfolios{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    width: 100%;
    row-gap: 2rem;
    column-gap: 1rem;
    @media screen and ( max-width: 1000px) {
        grid-template-columns: repeat(2,1fr);
    }
    @media screen and ( max-width: 600px) {
        grid-template-columns: repeat(1,1fr);
    }
    
    .portfolio{
        .description{
            font-weight: 300;
            font-size: 1rem;            
        }
        .image-data{
            margin-top: 3rem;
            position: relative;
            &::before{
                content: "";
                position: absolute;
                top: 12px;
                left: 15px;
                height: calc(100% - 30px);
                width: calc(100% - 30px);
                background-color: #ffffff9f;
                transform-origin: center;
                transform: scale(0);
                transition: all .4s ease-in-out;
                
            }
            &:hover::before{
                transform: scale(1);
            }
            img{
                width: 100%;
                height: 30vh;
                object-fit: cover;
            }
            .hover-items{
                list-style: none;
                position: absolute;
                top:70%;
                left: 50%;
                transform: translate(-50%,-50%);                
                padding: 1rem 2rem;
                visibility: hidden;
                li{                    
                    a{                        
                        padding: 1rem;
                        text-decoration: none;
                        font-family: inherit;
                        border-radius: 10px;
                        background-color: #345995;
                        color: white;
                        &:not(:last-child){
                            margin-right: 2rem;
                        }                       
                    }
                }
            }
            &:hover{
                .hover-items{
                    transition: all .5s ease-in-out .2s;
                    visibility: visible;
                    top: 50%;
                }
            }
        }
        
        h5{
            font-size: 1.7rem;
            font-weight: 500;
            color: white;
            transition: all .1s ease-in-out;
            &:hover{
                color: #F2AF29;
            }
        }
    }
}